import React from 'react';
import styled from 'styled-components';
import { device } from '../assets/data/mediaQueries';
import { uid } from 'react-uid';
import Logo from '../atoms/Logo';
import { ButtonOrder } from '../atoms/ButtonOrder';
import PanelName from '../atoms/PanelName';


const PopupWithOrderScreen = ({ togglePopup, newBurger, currentPrice, ingredients }) => {
  const mapIngredients = Object.entries(ingredients);
  const ingredientsList = mapIngredients.filter(item => item[1] !== 0).map(ingredient => (
    <PanelName key={uid(ingredient)}>{`${ingredient[0].charAt(0).toUpperCase() + ingredient[0].slice(1)}: ${ingredient[1]}`}</PanelName>
  ));

  return (
    <Wrapper onClick={togglePopup}>
      <WrapperInner>
        <PopupTitle>We received your order!</PopupTitle>
        <PanelName >Your ingredients:</PanelName>
        <IngredientWrapper>
          {ingredientsList}
        </IngredientWrapper>
        <PanelName currentPrice={currentPrice}>Prepare to pay: </PanelName>
        <CloseButton onClick={togglePopup}>
          X
        </CloseButton>
        <ButtonWrapper>
          <ButtonOrder newBurger={newBurger}>Create a new burger</ButtonOrder>
        </ButtonWrapper>
        <Logo />
      </WrapperInner>
    </Wrapper>
  )
};

export default PopupWithOrderScreen;

const Wrapper = styled.div`
  background-color: rgba(0,0,0,.8);
  height: 100vh;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1000;
  overflow: hidden;
`;

const WrapperInner = styled.div`
  background-image: radial-gradient(#5C5C5C , #000000);
  height: 90vh;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 40%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 0 10vw;
  align-items: center;

  @media ${device.tabletXL} {
    height: 100vh;
    width: 100%;
  } 
`;

const CloseButton = styled.button`
  border: 2px black solid;
  cursor: pointer;
  font-size: 20px;
  background-color: #FFCB00;
  color: black;
  position: absolute;
  top: 1vw;
  right: 1vw;
  transition: 1s;

  &:hover {
    border: 2px #FFCB00 solid;
    background-color: black;
    color: #FFCB00;  
  }
`;

const ButtonWrapper = styled.div`
  display: flex;
  align-self: center;
  flex-direction: column;
  width: 15vw;
`;

const PopupTitle = styled.h1`
  color: #FFCB00; 
  font-size: 40px;
  font-weight: 600;
  font-family: 'Yanone Kaffeesatz';
  text-align: center;
  padding-top: 20px;
  `;

const IngredientWrapper = styled.div`
  display: flex;
  flex-direction: column;

`;
